// src/components/Post.js
import React, { useState, useEffect, useRef } from "react";
import "../styles/Post.css";

const Post = ({ title, date, subSections, children }) => {
	// children prop을 추가하세요
	const contentRefs = useRef([]);

	const handleClick = (index) => {
		contentRefs.current[index].scrollIntoView({ behavior: "smooth" });
	};

	const [selectedSectionIndex, setSelectedSectionIndex] = useState(-1);

	useEffect(() => {
		const sectionHeights = subSections.map((section, index) => {
			const sectionElement = contentRefs.current[index];
			return sectionElement ? sectionElement.offsetHeight : 0;
		});

		const maxHeightIndex = sectionHeights.reduce(
			(maxIndex, height, index) =>
				height > sectionHeights[maxIndex] ? index : maxIndex,
			0
		);

		setSelectedSectionIndex(maxHeightIndex);
	}, []);

	{
		subSections &&
			subSections.map((section, index) => (
				<div
					ref={(el) => (contentRefs.current[index] = el)}
					key={index}
					className={`section ${
						index === selectedSectionIndex ? "selected" : ""
					}`}>
					<h2>{section.title}</h2>
					{section.content}
				</div>
			));
	}

	return (
		<div className="post-container">
			<div className="post-box">
				<h1 className="post-title">{title}</h1>
				<p className="post-date">{date}</p>
				{subSections && (
					<div className="table-of-contents">
						<ol>
							{subSections.map((section, index) => (
								<li
									key={index}
									onClick={() => handleClick(index)}>
									{section.title}
								</li>
							))}
						</ol>
					</div>
				)}
				<div className="post-content">
					{subSections &&
						subSections.map((section, index) => (
							<div
								ref={(el) => (contentRefs.current[index] = el)}
								key={index}>
								<h2>{section.title}</h2>
								{section.content}
							</div>
						))}
				</div>
				{children} {/* 이 줄을 추가하여 children을 렌더링하세요 */}
			</div>
		</div>
	);
};

export default Post;
